<template>
    <div>
        <van-popup v-model:show="route.isShowRegister" :style="{ height: '100%', width: '100%' }">
            <div class="box-style register-box">
                <div class="title-box">
                    <div>{{ $t('home.mySuperior') }}</div>
                    <div @click="clickCancel"><img src="@/assets/common/cancel-gray.png" alt=""></div>
                </div>
                <div class="input-box">
                    <van-field v-model="state.invitationAddress" :placeholder="$t('register.inputAddress')">
                    </van-field>
                </div>
                <div class="button-box">
                    <div class="button-style button-font"
                        :style="state.invitationAddress ? ('opacity: 1;') : ('opacity: 0.5;')" @click="clickConfirm">
                        {{ $t('trade.confirm') }}
                    </div>
                </div>
            </div>
        </van-popup>
    </div>
</template>

<script setup>

import { useRoute } from 'vue-router';
import { reactive, onMounted, getCurrentInstance } from 'vue';
import referral from '@/json/referral.json'
import staking from '@/json/SADHYCStaking.json'
import { ethers } from 'ethers';
import { Toast } from 'vant';
import i18n from '@/i18n';

const { proxy } = getCurrentInstance()
const route = useRoute()
const state = reactive({
    referralContractAddress: process.env.VUE_APP_REFERRAL_CONTRACT_ADDRESS,
    referralContract: null,
    stakingContractAddress: process.env.VUE_APP_SAD_HYC_STAKING_CONTRACT_ADDRESS,
    stakingContract: null,
    invitationAddress: ''
})

onMounted(async () => {
    state.referralContract = new ethers.Contract(state.referralContractAddress, referral, proxy.$provider.getSigner())
    state.stakingContract = new ethers.Contract(state.stakingContractAddress, staking, proxy.$provider.getSigner())
})

const clickCancel = (() => {
    route.isShowRegister = false
})

const clickConfirm = (async () => {
    if (state.invitationAddress) {
        Toast.loading()
        const root = await state.referralContract.root()
        if (state.invitationAddress.toLocaleLowerCase() == root.toLocaleLowerCase()) {
            startRegister()
        } else {
            Toast.loading()
            const hasStaked = await state.stakingContract.hasStaked(state.invitationAddress)
            if (hasStaked) {
                startRegister()
            } else {
                Toast.fail(i18n.global.t('toast.notRegister'))
            }
        }
    }
})
const startRegister = (async () => {
    Toast.loading()
    const register = await state.referralContract.register(state.invitationAddress)
    register.wait()
    Toast.success(i18n.global.t('toast.registerSuccess'))
    route.registered = true
    route.parent = state.invitationAddress
    route.isShowRegister = false
})

</script>

