<template>
    <div>
        <div class="top-menu-content">
            <div>
                <img class="top-logo" src="../assets/common/top-logo.png" @click="clickLogo">
            </div>
            <div class="top-menu-right-box">
                <div class="wallet-info" @click="clickConnectWallet">
                    {{ route.account ? $filters.addressFormat(route.account) : $t('topMenu.connectWallet') }}
                </div>
                <div>
                    <img class="top-menu" src="@/assets/common/top-menu.png" alt="" @click="clickMenu">
                </div>
            </div>
        </div>
        <Menu v-if="route.isShowMenu"></Menu>
        <Register v-if="route.isShowRegister"></Register>
        <ConnectWallet v-if="route.isShowConnectWallet"></ConnectWallet>
    </div>
</template>

<script setup>

import { reactive, onMounted, getCurrentInstance } from 'vue';
import { useRoute, useRouter } from 'vue-router';
import Menu from './Menu.vue';
import ConnectWallet from './ConnectWallet.vue';
import Register from './Register.vue';
import { Toast } from 'vant';
import referral from '@/json/referral.json'
import { AddressZero } from '@ethersproject/constants'
import { ethers } from 'ethers';

const { proxy } = getCurrentInstance()
const route = useRoute()
const router = useRouter()
const state = reactive({
referralContractAddress: process.env.VUE_APP_REFERRAL_CONTRACT_ADDRESS,
referralContract: null,
})

onMounted(async () => {
window.ethereum.on("accountsChanged", function (accounts) {
route.account = accounts[0]
route.isShowRegister = false
route.parent = ''
checkRegistered()
checkParent()
});
state.referralContract = new ethers.Contract(state.referralContractAddress, referral, proxy.$provider.getSigner())
clickConnectWallet()
})

const clickConnectWallet = (async () => {
if (!route.account) {
Toast.loading()
const accounts = await window.ethereum.request({ method: 'eth_requestAccounts' })
Toast.clear()
if (accounts[0]) {
route.account = accounts[0]
route.isConnectWallet = true
checkRegistered()
checkParent()
}
}
})

const checkRegistered = (async () => {
Toast.loading()
route.registered = await state.referralContract.registered(route.account)
if (!route.registered) {
    router.push('home')
route.isShowRegister = true
}
})
const checkParent = (async () => {
Toast.loading()
route.parent = await state.referralContract.parent(route.account)
if (route.parent === AddressZero) {
route.parent = ''
}
})

const clickLogo = (() => {
router.push('home')
})
const clickMenu = (() => {
route.isShowMenu = true
})

</script>

<style lang="less" scoped>
@import '@/common/base.less';

.top-menu-content {
    height: 44px;
    margin: 0 15px;
    display: flex;
    justify-content: space-between;

    .top-logo {
        height: 100%;
    }

    .top-menu-right-box {
        margin-top: 8px;
        height: 28px;
        display: flex;
        justify-content: space-between;

        .wallet-info {
            .scw(white, 13px, 400);
            line-height: 28px;
            border-radius: 8px;
            margin-right: 15px;
            padding: 0 10px;
            background: @gradientColorOne;
        }
    }

    .top-menu {
        padding-top: 2px;
        height: 24px;
    }
}
</style>