<template>
    <div>
        <van-popup v-model:show="route.isShowConnectWallet" :style="{ height: '100%', width: '100%' }">
            <div class="box-style connect-wallet-box">
                <div class="title-box">
                    <div>{{ $t('topMenu.connectWallet') }}</div>
                    <div @click="clickCancel"><img src="@/assets/common/cancel-gray.png" alt=""></div>
                </div>
                <div class="wallet-list-box">
                    <div v-for="item in state.walletList" :key="item.tag"
                        :class="(state.selectWalletTag == item.tag) ? state.selectBorder : state.unselectBorder"
                        @click="clickSelectWallet(item.tag)">
                        <div class="wallet-icon"><img :src="item.image"></div>
                        <div class="wallet-name">{{ item.name }}</div>
                    </div>
                </div>
            </div>
        </van-popup>
    </div>
</template>

<script setup>
import { useRoute } from 'vue-router';
import { reactive } from 'vue';

const state = reactive({
    walletList: [
        {
            tag: 0,
            name: 'TokenPocket',
            image: require('@/assets/account/tokenPocket.png')
        },
        {
            tag: 1,
            name: 'Metamask',
            image: require('@/assets/account/metamask.png')
        },
        {
            tag: 2,
            name: 'WalletConnect',
            image: require('@/assets/account/walletConnect.png')
        },
        {
            tag: 3,
            name: 'Coinbase Wallet',
            image: require('@/assets/account/coinbase.png')
        }
    ],
    selectWalletTag: -1,
    selectBorder: 'wallet-item-info select-border',
    unselectBorder: 'wallet-item-info unselect-border'
})

const route = useRoute()

const clickCancel = (() => {
    route.isShowConnectWallet = false
})
const clickSelectWallet = (async (tag) => {
    if (state.selectWalletTag != tag) {
        state.selectWalletTag = tag
        if (window.ethereum.isMetaMask) {
            const accounts = await window.ethereum.request({ method: 'eth_requestAccounts' })
            if (accounts[0]) {
                route.account = accounts[0]
            }
            clickCancel()
        } else {
            console.log('未连接metaMask')
        }
    }
})

</script>

