import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import 'lib-flexible/flexible'
import vueI18n from './i18n'
import 'vant/lib/index.less'
import './common/vant.less'
import { Col, CountDown, Empty, Field, Image, List, Popover, Popup, Row, Swipe, SwipeItem, Tab, Tabs } from 'vant'
import { ethers } from 'ethers'

const app = createApp(App)

app.config.globalProperties.$provider = new ethers.providers.Web3Provider(window.ethereum)

app.config.globalProperties.$filters = {
    addressFormat(address) {
        if (address) {
            return `${address.substr(0, 6)}......${address.substr(-4)}`
        } else {
            return ''
        }
    },
}

app.use(router)
    .use(vueI18n)
    .use(Swipe)
    .use(SwipeItem)
    .use(Image)
    .use(Popup)
    .use(CountDown)
    .use(Tab)
    .use(Tabs)
    .use(List)
    .use(Empty)
    .use(Field)
    .use(Col)
    .use(Row)
    .use(Popover)

app.mount('#app')
