<template>
    <div>
        <van-popup v-model:show="route.isShowMenu" :style="{ height: '100%', width: '100%' }" :overlay="false">
            <div class="menu-box">
                <div class="top-menu-content">
                    <div>
                        <img class="top-menu top-menu-cancel" src="@/assets/common/cancel.png" alt=""
                            @click="clickMenu">
                    </div>
                </div>
                <div class="list-box">
                    <div class="list-item" v-for="item in state.menuList" :key="item.title"
                        @click="clickMenuItem(item.path)">
                        <div><img :src="item.icon"></div>
                        <div class="item-title"
                            :style="(item.title == $t('topMenu.language')) ? state.languageWidth : state.otherWidth">
                            {{ item.title }}
                        </div>
                        <div v-if="item.title == $t('topMenu.language')">
                            <img :src="state.isShowLanguage ? arrowIcon.up : arrowIcon.down">
                        </div>
                    </div>
                </div>
                <div class="box-style language-box" v-if="state.isShowLanguage">
                    <div class="padding-box">
                        <div class="language-title"
                            :style="(state.language == 'zh') ? state.selectLanguageColor : state.unSelectLanguageColor"
                            @click="clickSelectLanguage('zh')">
                            简体中文
                        </div>
                        <div class="language-title"
                            :style="(state.language == 'en') ? state.selectLanguageColor : state.unSelectLanguageColor"
                            @click="clickSelectLanguage('en')">
                            English
                        </div>
                    </div>
                </div>
            </div>
        </van-popup>
    </div>
</template>

<script setup>
import { reactive, onMounted } from 'vue';
import { useRouter, useRoute } from 'vue-router';
import i18n from '@/i18n';
import { Toast } from 'vant';

const router = useRouter()
const route = useRoute()
const state = reactive({
    menuList: [
        {
            icon: require('@/assets/common/menu/home.png'),
            title: i18n.global.t('topMenu.home'),
            path: 'home'
        },
        {
            icon: require('@/assets/common/menu/trade.png'),
            title: i18n.global.t('topMenu.trade'),
            path: 'trade'
        },
        {
            icon: require('@/assets/common/menu/hyc-fluidity.png'),
            title: 'HYC ' + i18n.global.t('home.fluidity'),
            path: 'hycFluidity'
        },
        {
            icon: require('@/assets/common/menu/sad-fluidity.png'),
            title: 'SAD ' + i18n.global.t('home.fluidity'),
            path: 'sadFluidity'
        },
        {
            icon: require('@/assets/common/menu/sad-staking.png'),
            title: 'SAD ' + i18n.global.t('home.staking'),
            path: 'sadStaking'
        },
        {
            icon: require('@/assets/common/menu/account.png'),
            title: i18n.global.t('topMenu.account'),
            path: 'account'
        },
        {
            icon: require('@/assets/common/menu/language.png'),
            title: i18n.global.t('topMenu.language'),
            path: ''
        },
    ],
    language: 'zh',
    isShowLanguage: false,
    languageWidth: {
        width: 'calc(100% - 72px)'
    },
    otherWidth: {
        width: 'calc(100% - 36px)'
    },
    selectLanguageColor: {
        color: '#E4EAEA'
    },
    unSelectLanguageColor: {
        color: '#7B8083'
    }
})

const arrowIcon = {
    up: require('@/assets/common/menu/up-arrow.png'),
    down: require('@/assets/common/menu/down-arrow.png')
}

onMounted(async () => {
    state.language = localStorage.getItem('locale')
})

const clickMenu = (() => {
    route.isShowMenu = false
})

const checkUserState = (() => {
    if (!route.account) {
        Toast.fail(i18n.global.t('toast.pleaseConnectWallet'))
        return false
    }
    if (!route.registered) {
        Toast.fail(i18n.global.t('toast.pleaseRegister'))
        route.isShowRegister = true
        return false
    }
    return true
})

const clickMenuItem = ((path) => {
    if (path) {
        if (checkUserState()) {
            route.isShowMenu = false
            router.push(path)
        }
    } else {
        state.isShowLanguage = state.isShowLanguage ? false : true
    }
})
const clickSelectLanguage = ((language) => {
    if (state.language != language) {
        localStorage.setItem('locale', language)
        i18n.global.locale = language
        state.language = language
        route.isShowMenu = false
    }
})

</script>

